.detail {
  margin-top: 15px;
  padding: 30px;
  background-color: #fff;
  border: 1px solid #ebedf0;
}
.detail .detail-info {
  display: flex;
  align-items: center;
}
.detail .detail-info .detail-info-name {
  margin-left: 10px;
}
.detail .detail-info .detail-info-name h3 {
  color: #009a61;
  font-weight: bold;
  font-size: 14x;
}
.detail .detail-info .detail-info-name p {
  font-size: 14px;
  margin-top: 7px;
  color: #999;
}
.detail .detail-title {
  font-size: 30px;
  line-height: 42px;
  margin-top: 15px;
  margin-bottom: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "PingFang SC", "Microsoft YaHei", "Source Han Sans SC", "Noto Sans CJK SC", "WenQuanYi Micro Hei", sans-serif;
}
.detail .detail-content {
  margin-bottom: 100px;
}
.detail .detail-content p {
  line-height: 25px;
}
